  },
  methods: {
    dropLinkClick(link) {
      if (link.name === "home") {
        window.location.href = "/";
      }
      if (link.name === "portfolio") {
        this.dropDownLinks = this.allDropOptions.slice(4, 10);
      }
      if (link.index !== undefined) {
        this.chooseCat(link.index, true);
      }
    },
    createDropLinks() {
      this.dropDownLinks = this.allDropOptions.slice(0, 4);
    },
    showDropDown() {
      if (this.dropDown) {
        this.closeDropDown();
      } else {
        this.openDropDown();
      }
    },
    closeDropDown() {
      let createDropLinks = this.createDropLinks;
      let dropMenu = document.querySelector(".drop-down__list");
      let cross = document.querySelector(".drop-down__cross");
      let dropBtn = document.querySelector(".drop-down__btn");
      dropMenu.classList.remove("show-dropdown");
      cross.classList.remove("show-cross");
      dropBtn.classList.remove("drop-down__btn--clicked");
      setTimeout(function() {
        dropMenu.classList.remove("show-dropdown-scale");
        createDropLinks();
      }, 600);
      this.dropDown = false;
    },
    openDropDown() {
      let dropMenu = document.querySelector(".drop-down__list");
      let cross = document.querySelector(".drop-down__cross");
      let dropBtn = document.querySelector(".drop-down__btn");
      let closeDropDown = this.closeDropDown;
      cross.onclick = function() {
        cross.classList.remove("show-cross");
        dropBtn.classList.remove("drop-down__btn--clicked");
        closeDropDown();
      };
      dropMenu.classList.add("show-dropdown", "show-dropdown-scale");
      cross.classList.add("show-cross");
      dropBtn.classList.add("drop-down__btn--clicked");
      this.dropDown = true;
    },
    checkIfMobile() {
      if (window.innerWidth < 769) {
        this.screenSize = "mob";
      } else if (window.innerWidth < 1440) {
        this.screenSize = "mid";
      } else {
        this.screenSize = "large";
      }
    },
    chooseCat(index, drop) {
      if (drop) {
        this.closeDropDown();
      }
      this.activeMode = 0;
      this.fadeOut();
      let cat = this.photos[index];
      this.photosOnPage = [];

      cat.albums.forEach(album => {
        let pack = {
          album: album,
          url: album.url + "1"
        };
        this.photosOnPage.push(pack);
      });

      this.rows = [];
      this.makeRows();
      this.activeCat = index;
      for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
        pic.classList.remove("clicked");
      }
    },

    addClicked(e) {
      if (this.screenSize !== "mob") {
        let el;

        if (e.target.parentElement.classList[0] === "galery__img-wrapper") {
          el = e.target.parentElement;
        } else {
          el = e.target.parentElement.parentElement;
        }

        if (!el.classList.contains("clicked")) {
          for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
            pic.classList.remove("clicked");
          }

          el.classList.add("clicked");
        }
      }
    },

    chooseAlbum(photo, e) {
      let el;

      if (e.target.parentElement.classList[0] === "galery__img-wrapper") {
        el = e.target.parentElement;
      } else {
        el = e.target.parentElement.parentElement;
      }

      if (el.classList.contains("clicked")) {
        this.fadeOut();
        if (this.activeMode === 0) {
          this.photosOnPage = [];

          let cat = this.photos[photo.album.catIndex - 1];

          let album = cat.albums[photo.album.albumIndex];

          for (let i = 0; i < album.length; i++) {
            let pack = {
              album: album,
              url: album.url + (i + 1 + "")
            };

            this.photosOnPage.push(pack);
          }

          this.rows = [];
          this.makeRows();
          this.activeMode = 1;
          for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
            pic.classList.remove("clicked");
          }
        }
      } else {
        for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
          pic.classList.remove("clicked");
        }

        el.classList.add("clicked");
      }
    },
    fadeOut() {
      if (this.activeMode === 0) {
        for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
          pic.classList.add("fade-out");
        }
      }
    },
    loadAll(clear) {
      if (clear) {
        this.photosOnPage = [];
        this.rows = [];
      }
      if (this.activeMode === 0) {
        this.shufleNumbers();

        let index = 0;

        let cat;

        let randomAlbum = 0;

        let albumsQuantity;

        let album;

        for (let i = 0; i < 9; i++) {
          if (index === 7) {
            index = 0;
          }

          let url = "";

          cat = this.photos[this.order[index]];

          albumsQuantity = cat.albums.length;

          randomAlbum = this.getRndInteger(0, albumsQuantity);

          album = cat.albums[randomAlbum];

          let photosQuantity = album.length;

          let randomPhoto = this.getRndInteger(1, photosQuantity + 1);

          url += album.url + randomPhoto;

          let photo = {
            url: url,
            album: album
          };

          this.photosOnPage.push(photo);

          index++;
        }

        this.makeRows();
        this.activeCat = -1;
      }
    },
    makeRows() {
      let rowQuantity = 3;
      if (this.screenSize === "mob") {
        rowQuantity = 2;
      } else if (this.screenSize === "mid") {
        rowQuantity = 3;
      } else {
        rowQuantity = 4;
      }

      for (let i = 0; i < rowQuantity; i++) {
        this.rows.push([]);
      }

      let row = 0;
      for (let i = 0; i < this.photosOnPage.length; i++) {
        if (row === rowQuantity) {
          row = 0;
        }
        this.rows[row].push(this.photosOnPage[i]);

        row++;
      }

      setTimeout(this.fadeIn, 1000);
    },
    fadeIn() {
      for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
        pic.classList.remove("fade-out");
        pic.classList.add("fade-in");
      }
    },
    testFunc() {
      console.log("test");
    },

    shufleNumbers() {
      this.shufleCategories(this.order);
    },

    shufleCategories(arr) {
      let newArr = arr;
      let currentIndex = newArr.length;
      let temporaryValue, randomIndex;

      while (0 !== currentIndex) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        temporaryValue = newArr[currentIndex];
        newArr[currentIndex] = newArr[randomIndex];
        newArr[randomIndex] = temporaryValue;
      }
      let randomizedArray = newArr;
      return randomizedArray;
    },
    getRndInteger(min, max) {
      return Math.floor(Math.random() * (max - min)) + min;
    },
    resize() {
      let func = this.checkIfMobile;
      window.onresize = function() {
        func();
      };
    }
  },

  mounted() {
    // this.testFunc();
    this.activeMode = 0;

    this.checkIfMobile();

    this.createDropLinks();

    this.resize();

    this.loadAll();
  }
